import React from 'react';
import { AppBar, Box, Button, Container, IconButton, Link, Menu, MenuItem, Toolbar, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { useNavigate, Link as RouterLink } from 'react-router-dom';


const pages = [{ title: 'Study Material', link: '/studymaterial' }, { title: 'Societies', link: '/societies' }, { title: 'DepC Fundae', link: '/depc' }, { title: 'KGP Fundae', link: '/kgpFundae' }, { title: 'Competitions', link: '/competitions' }, { title: 'Our Team', link: '/ourTeam' }];

const NavBar = () => {
  const navigate = useNavigate();
  const [anchorElNav, setAnchorElNav] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };


  return (
    <AppBar sx={{ bgcolor: 'font.darker' }} position="fixed" shadow={0}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Link
            underline="none"
            to="/"
            variant="h6"
            noWrap
            component={RouterLink}
            sx={{ mr: 2, flexGrow: 1, color: 'font.light', fontWeight:'700', display: { xs: 'none', md: 'flex' }}}
          >
            KGP Launchpad
          </Link>

          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' }}}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              sx={{ color: 'font.light' }}
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page.title} onClick={() => {handleCloseNavMenu(); navigate(page.link);}}>
                  <Typography textAlign="center" color={window.innerWidth > 1000 ? 'font.light' : 'font.dark'}>{page.title}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <Link
            underline="none"
            to="/"
            variant="h6"
            noWrap
            component={RouterLink}
            sx={{ flexGrow: 1, color: 'font.light', fontWeight:'800', display: { xs: 'flex', md: 'none' }}}
          >
            KGP Launchpad
          </Link>
          <Box sx={{ flexGrow: 0, display: { xs: 'none', md: 'flex' }}}>
            {pages.map((page) => (
              <Button
                key={page.title}
                onClick={() => navigate(page.link)}
                sx={{ my: 2, mx:1, color: 'font.light', display: 'block', fontWeight:'600' }}
              >
                {page.title}
              </Button>
            ))}
          </Box>

        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default NavBar;
