import { getAll } from '../service';

const kgpFundaePorReducer = (state = [], action = {}) => {
  switch (action.type) {
    case 'INIT_KGP_FUNDAE_PORS': {
      return action.data;
    }
    default: {
      return state;
    }
  }
};

export const initialiseKgpFundaePors = () => async (dispatch) => {
  const data = await getAll('kgp_fundae/por')();
  dispatch({
    type: 'INIT_KGP_FUNDAE_PORS',
    data,
  });
};

export default kgpFundaePorReducer;
