import { getAll } from '../service';

const SafetyReducer = (state = [], action = {}) => {
  switch (action.type) {
    case 'INIT_SAFETY': {
      return action.data;
    }
    default: {
      return state;
    }
  }
};

export const initialiseSafety = () => async (dispatch) => {
  const data = await getAll('coc')();
  dispatch({
    type: 'INIT_SAFETY',
    data,
  });
};

export default SafetyReducer;
