import React from 'react';
import { ThemeProvider } from '@mui/material/styles';
import theme from '../theme/customTheme';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import PropTypes from 'prop-types';
import background from '../assets/img/footerDark.png';
import { Link } from 'react-router-dom';

const pages = [{ title: 'Our Team', link: '/ourTeam' }, { title: 'Competition & Challenges', link: '/competitions' }, { title: 'Department Change', link: '/depc' }, { title: 'Study Material', link: '/studymaterial' }, { title: 'Societies', link: '/societies' }, { title: 'KGP Fundae', link: '/kgpFundae' }, { title: 'Contact Us', link: '/' }];

function Footer(props){
  const { sx } = props;
  return (<ThemeProvider theme={theme}>
    <Box sx={{ height:'23vh', ...sx, width:'100%', display:'flex', flexDirection:'row', marginTop:'3em', backgroundColor:'bg.dark', justifyContent:'space-between' }}>
      <Box sx={{ display:'flex', height:'100%', justifyContent:'center', alignContent:'center', padding:'auto', flexDirection:['column', 'row'], alignItems:'center', textAlign:['center','left']}}>
        <img src={background} style={{ height:'80%', margin:'auto' }} />
        <Typography align="center" variant="h6" sx={{ fontSize:['1rem', '2rem'], fontWeight:'700', color:'font.light' }}>KGP Launchpad</Typography>
      </Box>
      <Box sx={{ display:'flex', width:['60%', '50%'], flexWrap:'wrap', justifyContent:'space-evenly', alignItems:'center' }}>
        {pages.map((page) => (
          <Typography component={Link} to={page.link}  variant="body2" sx={{ margin:'1%', fontSize:['0.7rem','0.7rem', '1.2rem'], width:['46%','30%'], color:'font.light', fontWeight:'600', textDecoration:'none' }} key={page.title}>{page.title}</Typography>
        ))}
      </Box>
    </Box>
  </ThemeProvider>);
}

Footer.propTypes = {
  sx: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.func, PropTypes.object])),
    PropTypes.func,
    PropTypes.object,
  ]),
};

export default Footer;