import { getAll } from '../service';

const inspiringKgpianReducer = (state = [], action = {}) => {
  switch (action.type) {
    case 'INIT_INSPIRING_KGPIANS': {
      return action.data;
    }
    default: {
      return state;
    }
  }
};

export const initialiseInspiringKgpians = () => async (dispatch) => {
  const data = await getAll('inspiring_kgpians')();
  dispatch({
    type: 'INIT_INSPIRING_KGPIANS',
    data,
  });
};

export default inspiringKgpianReducer;
