import { getAll } from '../service';

const studySubjectReducer = (state = [], action = {}) => {
  switch (action.type) {
    case 'INIT_STUDY_SUBJECTS': {
      return action.data;
    }
    default: {
      return state;
    }
  }
};

export const initialiseStudySubjects = () => async (dispatch) => {
  const data = await getAll('study/subjects')();
  dispatch({
    type: 'INIT_STUDY_SUBJECTS',
    data,
  });
};

export default studySubjectReducer;
